* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}
input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p {
  text-align: center;
  font-weight: bold;
}
.card img {
  max-width: 100%;
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
